// src/components/VideoPlayer.jsx
import React, { useEffect, useRef, useState } from 'react';
import Hls from 'hls.js';

function VideoPlayer({ url, config, contentType }) {
  const videoRef = useRef(null);
  const hlsRef = useRef(null); // Mantener referencia de HLS para evitar recrearlo constantemente
  const [isMp4, setIsMp4] = useState(false);

  useEffect(() => {
    if (!url) {
      console.warn('No URL provided for video playback');
      return;
    }

    // Verificamos si la URL apunta a un archivo .mp4
    if (url.includes('.mp4')) {
      setIsMp4(true);
    } else {
      setIsMp4(false);
    }

    if (Hls.isSupported() && !isMp4) {
      if (hlsRef.current) {
        console.log('Destroying existing HLS instance before re-initializing');
        hlsRef.current.destroy();
      }

      console.log('HLS is supported, initializing HLS.js');
      const hls = new Hls({
        xhrSetup: (xhr, url) => {
          console.log(`Setting up XHR request for URL: ${url}`);
          xhr.withCredentials = false; // Removemos las credenciales para evitar conflictos CORS
        },
        maxBufferLength: 60, // Reducir el tiempo de buffer para depuración
        manifestLoadingMaxRetry: 3, // Reducir reintentos para acortar el ciclo de prueba
        manifestLoadingRetryDelay: 1000, // Reducir el retraso entre intentos
        manifestLoadingTimeOut: 30000, // Reducir el tiempo de espera para la carga del manifiesto
        levelLoadingTimeOut: 20000, // Reducir el tiempo de espera para la carga del nivel
      });
      hls.loadSource(url);
      hls.attachMedia(videoRef.current);
      hlsRef.current = hls;

      hls.on(Hls.Events.MANIFEST_PARSED, function () {
        console.log('Manifest parsed successfully, starting playback');
        videoRef.current.play().catch((err) => {
          console.error('Error trying to play video after manifest parsed:', err);
        });
      });

      hls.on(Hls.Events.MEDIA_ATTACHED, function () {
        console.log('Media attached to HLS instance');
      });

      hls.on(Hls.Events.LEVEL_LOADED, function (event, data) {
        console.log(`Level loaded: Level ${data.level}, Duration: ${data.details.totalduration}s`);
      });

      hls.on(Hls.Events.FRAG_LOADED, function (event, data) {
        console.log(`Fragment loaded: SN ${data.frag.sn}, Level: ${data.frag.level}`);
      });

      hls.on(Hls.Events.ERROR, function (event, data) {
        console.error('HLS error occurred:', data);
        if (data.fatal) {
          switch (data.type) {
            case Hls.ErrorTypes.NETWORK_ERROR:
              console.error("Network error encountered. Retrying...");
              hls.startLoad();
              break;
            case Hls.ErrorTypes.MEDIA_ERROR:
              console.error("Media error encountered. Attempting to recover...");
              hls.recoverMediaError();
              break;
            default:
              console.error("Fatal error encountered. Destroying HLS instance.");
              hls.destroy();
              hlsRef.current = null;
              break;
          }
        } else {
          console.warn(`Non-fatal error occurred: ${data.type} - ${data.details}`);
        }
      });
    } else if (isMp4) {
      console.log('URL points to an MP4 file, using direct video source');
      videoRef.current.src = url;
      videoRef.current.addEventListener('loadedmetadata', () => {
        console.log('Metadata loaded, attempting to play video');
        videoRef.current.play().catch((err) => {
          console.error('Error trying to play video:', err);
        });
      });
    } else if (videoRef.current.canPlayType('application/vnd.apple.mpegurl')) {
      console.log('HLS not supported natively, using direct video source');
      videoRef.current.src = url;
      videoRef.current.addEventListener('loadedmetadata', () => {
        console.log('Metadata loaded, attempting to play video');
        videoRef.current.play().catch((err) => {
          console.error('Error trying to play video:', err);
        });
      });
    }

    return () => {
      if (hlsRef.current) {
        console.log('Cleaning up HLS instance');
        hlsRef.current.destroy();
        hlsRef.current = null;
      }
    };
  }, [url, isMp4]);

  return (
    <div className="video-player-container">
      <video ref={videoRef} controls autoPlay style={{ width: '100%' }} />
    </div>
  );
}

export default VideoPlayer;
