// src/components/ContentList.jsx
import React from 'react';
import './ContentList.css';

function ContentList({ content, contentType, setSelectedUrl, config, fetchEpisodes }) {
  if (!content || !Array.isArray(content)) {
    return <div>No hay contenido disponible</div>;
  }

  if (contentType === 'episodes') {
    // Agrupar episodios por temporada
    const episodesBySeason = content.reduce((acc, episode) => {
      if (!acc[episode.season]) {
        acc[episode.season] = [];
      }
      acc[episode.season].push(episode);
      return acc;
    }, {});

    return (
      <div className="categories-container">
        {Object.entries(episodesBySeason).map(([seasonNumber, episodes]) => (
          <div key={`season-${seasonNumber}`} className="category-item">
            <h3>Temporada {seasonNumber}</h3>
            {episodes.map((episode) => (
              <div key={episode.id || episode.episode_num} className="content-item">
                <img
                  className="content-image"
                  src={episode.info?.movie_image || 'https://via.placeholder.com/150'}
                  alt={episode.title || `Episodio ${episode.episode_num}`}
                />
                <h4>{episode.title || `Episodio ${episode.episode_num}`}</h4>
                <button
                  className="view-category-button"
                  onClick={() => setSelectedUrl(`${config.host}/series/${config.user}/${config.password}/${episode.id}.m3u8`)}
                >
                  Ver Episodio
                </button>
              </div>
            ))}
          </div>
        ))}
      </div>
    );
  }

  return (
    <div className="categories-container">
      {content.length > 0 ? (
        content.map((item, index) => (
          <div key={item.stream_id || item.series_id || item.movie_id || index} className="category-item">
            <img
              className="content-image"
              src={
                contentType === 'series' ? item.cover || 'https://via.placeholder.com/150' :
                item.stream_icon || 'https://via.placeholder.com/150'
              }
              alt={item.name || item.title}
            />
            <h3>{item.name || item.title}</h3>
            {contentType === 'live' ? (
              <button
                className="view-category-button"
                onClick={() => setSelectedUrl(`${config.host}/live/${config.user}/${config.password}/${item.stream_id}.m3u8`)}
              >
                Ver Canal
              </button>
            ) : contentType === 'vod' ? (
              <button
                className="view-category-button"
                onClick={() => setSelectedUrl(`${config.host}/movie/${config.user}/${config.password}/${item.stream_id}.m3u8`)}
              >
                Ver Película
              </button>
            ) : contentType === 'series' && fetchEpisodes ? (
              <button
                className="view-category-button"
                onClick={() => fetchEpisodes(item.series_id)}
              >
                Ver Serie
              </button>
            ) : null}
          </div>
        ))
      ) : (
        <div>No hay contenido disponible</div>
      )}
    </div>
  );
}

export default ContentList;
