// src/components/ConfigForm.jsx
import React, { useState, useEffect } from 'react';

function ConfigForm({ onSaveConfig }) {
  const [host, setHost] = useState('');
  const [user, setUser] = useState('');
  const [password, setPassword] = useState('');

  useEffect(() => {
    // Cargar configuraciones almacenadas en localStorage
    const savedConfig = JSON.parse(localStorage.getItem('iptvConfig'));
    if (savedConfig) {
      setHost(savedConfig.host);
      setUser(savedConfig.user);
      setPassword(savedConfig.password);
    }
  }, []);

  const handleSave = () => {
    const config = { host, user, password };
    localStorage.setItem('iptvConfig', JSON.stringify(config));
    onSaveConfig(config);
  };

  return (
    <div>
      <h2>Configuración del Servidor IPTV</h2>
      <input
        type="text"
        placeholder="Host"
        value={host}
        onChange={(e) => setHost(e.target.value)}
      />
      <input
        type="text"
        placeholder="Usuario"
        value={user}
        onChange={(e) => setUser(e.target.value)}
      />
      <input
        type="password"
        placeholder="Contraseña"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />
      <button onClick={handleSave}>Guardar Configuración</button>
    </div>
  );
}

export default ConfigForm;
