// src/App.jsx
import React, { useState, useEffect } from 'react';
import ConfigForm from './components/ConfigForm';
import VideoPlayer from './components/VideoPlayer';
import ContentList from './components/ContentList';
import CategoriesList from './components/CategoriesList';

function App() {
  const [config, setConfig] = useState(null);
  const [categories, setCategories] = useState([]);
  const [content, setContent] = useState([]);
  const [episodes, setEpisodes] = useState([]);
  const [selectedUrl, setSelectedUrl] = useState(null);
  const [currentView, setCurrentView] = useState('home'); // home, categories, content, episodes
  const [contentType, setContentType] = useState(''); // live, vod, series
  const [showConfig, setShowConfig] = useState(false);
  const [loading, setLoading] = useState(false); // Estado para manejar la carga

  useEffect(() => {
    const savedConfig = JSON.parse(localStorage.getItem('iptvConfig'));
    if (savedConfig) {
      setConfig(savedConfig);
    }
  }, []);

  const fetchCategories = async (type) => {
    if (config && config.host && config.user && config.password) {
      try {
        setLoading(true);
        setContentType(type);
        const url = `${config.host}/player_api.php?username=${config.user}&password=${config.password}&action=get_${type}_categories`;
        const response = await fetch(url);

        if (!response.ok) {
          throw new Error(`Error al obtener categorías de ${type}`);
        }

        const data = await response.json();
        setCategories(data);
        setCurrentView('categories');
      } catch (error) {
        console.error(`Error fetching ${type} categories:`, error);
      } finally {
        setLoading(false);
      }
    }
  };

  const fetchContentByCategory = async (categoryId) => {
    if (config && config.host && config.user && config.password && categoryId) {
      try {
        setLoading(true);
        const action = contentType === 'live' ? 'get_live_streams' : contentType === 'vod' ? 'get_vod_streams' : 'get_series';
        const url = `${config.host}/player_api.php?username=${config.user}&password=${config.password}&action=${action}&category_id=${categoryId}`;
        const response = await fetch(url);

        if (!response.ok) {
          throw new Error(`Error al obtener el contenido de la categoría ${categoryId}`);
        }

        const data = await response.json();
        setContent(data);
        setCurrentView('content');
      } catch (error) {
        console.error('Error fetching content by category:', error);
      } finally {
        setLoading(false);
      }
    }
  };

  const fetchEpisodes = async (seriesId) => {
    if (config && config.host && config.user && config.password && seriesId) {
      try {
        setLoading(true);
        const url = `${config.host}/player_api.php?username=${config.user}&password=${config.password}&action=get_series_info&series_id=${seriesId}`;
        const response = await fetch(url);

        if (!response.ok) {
          throw new Error(`Error al obtener episodios de la serie ${seriesId}`);
        }

        const data = await response.json();
        if (data.episodes) {
          const episodesList = Object.values(data.episodes).flat(); // Convertir los episodios a un solo array
          setEpisodes(episodesList);
          setCurrentView('episodes');
        }
      } catch (error) {
        console.error('Error fetching episodes:', error);
      } finally {
        setLoading(false);
      }
    }
  };

  const handleFetch = (type) => {
    fetchCategories(type);
  };

  const handleBack = () => {
    if (currentView === 'episodes') {
      setCurrentView('content');
    } else if (currentView === 'content') {
      setCurrentView('categories');
    } else if (currentView === 'categories') {
      setCurrentView('home');
    }
    setSelectedUrl(null);
  };

  const handleHome = () => {
    setCurrentView('home');
    setSelectedUrl(null);
  };

  const toggleConfig = () => {
    setShowConfig(!showConfig);
  };

  return (
    <div>
      <div style={{ position: 'fixed', top: '10px', right: '10px', cursor: 'pointer' }}>
        <button onClick={toggleConfig}>&#9776; Configuración</button>
      </div>

      {showConfig && <ConfigForm onSaveConfig={setConfig} />}

      {loading && <div className="loading">Cargando...</div>}

      {currentView !== 'home' && (
        <div className="navigation-buttons">
          <button onClick={handleBack}>Volver Atrás</button>
          <button onClick={handleHome}>Ir al Inicio</button>
        </div>
      )}
      {config && currentView === 'home' && (
        <div>
          <button onClick={() => handleFetch('live')}>TV en Vivo</button>
          {/*<button onClick={() => handleFetch('vod')}>Películas</button>
          <button onClick={() => handleFetch('series')}>Series</button>*/}
        </div>
      )}
      {currentView === 'categories' && (
        <CategoriesList categories={categories} onSelectCategory={fetchContentByCategory} contentType={contentType} />
      )}
      {currentView === 'content' && !selectedUrl && (
        <ContentList
          content={content}
          contentType={contentType}
          setSelectedUrl={setSelectedUrl}
          config={config}
          fetchEpisodes={contentType === 'series' ? fetchEpisodes : undefined} // Pasamos fetchEpisodes solo si es series
        />
      )}
      {currentView === 'episodes' && !selectedUrl && (
        <ContentList
          content={episodes}
          contentType="episodes"
          setSelectedUrl={setSelectedUrl}
          config={config}
        />
      )}
      {selectedUrl && <VideoPlayer url={selectedUrl} config={config} contentType={contentType} />} {/* Pasar config y contentType al VideoPlayer */}
    </div>
  );
}

export default App;
