// src/components/CategoriesList.jsx
import React from 'react';
import './CategoriesList.css';

function CategoriesList({ categories, onSelectCategory, contentType }) {
  // Asegúrate de que `categories` sea un array válido
  const validCategories = Array.isArray(categories) ? categories : [];

  return (
    <div className="categories-container">
      {validCategories.length > 0 ? (
        validCategories.map((category) => (
          <div key={category.category_id} className="category-item">
            <h3>{category.category_name}</h3>
            <button
              className="view-category-button"
              onClick={() => onSelectCategory(category.category_id)}
            >
              {contentType === 'live' ? 'Ver Canales' : contentType === 'vod' ? 'Ver Películas' : 'Ver Series'}
            </button>
          </div>
        ))
      ) : (
        <div>No hay categorías disponibles</div>
      )}
    </div>
  );
}

export default CategoriesList;
